<template>
    <div class="calculator">
        <div class="display">{{ display }}</div>
        <div class="buttons">
            <button v-for="button in buttons" :key="button" @click="handleClick(button)">{{ button }}</button>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            display: '0',
            buttons: ['C', 'CE', '%', '/', '7', '8', '9', '*', '4', '5', '6', '-', '1', '2', '3', '+', '0', '.', '='],
            operator: null,
            operand1: null,
            operand2: null,
            result: null
        }
    },
    methods: {
        handleClick(button) {
            switch (button) {
                case 'C':
                    this.clearDisplay();
                    break;
                case 'CE':
                    this.clearEntry();
                    break;
                case '%':
                    this.percent();
                    break;
                case '/':
                case '*':
                case '-':
                case '+':
                    this.setOperator(button);
                    break;
                case '=':
                    this.calculate();
                    break;
                default:
                    this.appendDigit(button);
                    break;
            }
        },
        clearDisplay() {
            this.display = '0';
            this.operator = null;
            this.operand1 = null;
            this.operand2 = null;
            this.result = null;
        },
        clearEntry() {
            this.display = '0';
        },
        percent() {
            const value = parseFloat(this.display) / 100;
            this.display = value.toString();
        },
        setOperator(operator) {
            this.operator = operator;
            this.operand1 = parseFloat(this.display);
            this.display = '0';
        },
        appendDigit(digit) {
            if (this.display === '0') {
                this.display = digit;
            } else {
                this.display += digit;
            }
        },
        calculate() {
            this.operand2 = parseFloat(this.display);
            switch (this.operator) {
                case '/':
                    this.result = this.operand1 / this.operand2;
                    break;
                case '*':
                    this.result = this.operand1 * this.operand2;
                    break;
                case '-':
                    this.result = this.operand1 - this.operand2;
                    break;
                case '+':
                    this.result = this.operand1 + this.operand2;
                    break;
            }
            this.display = this.result.toString();
            this.operator = null;
            this.operand1 = null;
            this.operand2 = null;
        }
    }
}
</script>

<style>
.calculator {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 300px;
    height: 400px;
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 20px;
}

.display {
    width: 100%;
    height: 80px;
    font-size: 36px;
    text-align: right;
    padding: 10px;
    margin-bottom: 20px;
    background-color: #f5f5f5;
    border: 1px solid #ccc;
    border-radius: 5px;
}

.buttons {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 10px;
}

button {
    width: 100%;
    height: 60px;
    font-size: 24px;
    border: none;
    border-radius: 5px;
    background-color: #f5f5f5;
    cursor: pointer;
    transition: background-color 0.2s ease-in-out;
}

button:hover {
    background-color: #ccc;
}
</style>